import React from "react";

const PageHeader = ({ className }) => {
  return (
    <section className={className}>
      <div className="content">
        <h1 className="big-heading">{"We are here to Help!"}</h1>
        <p>{"Tell us how we can help you today."}</p>
        <p>{"We will be happy to listen to you and assist."}</p>
      </div>
    </section>
  );
};

export default PageHeader;
