import React from "react";

const HotLines = ({ className }) => {
  return (
    <section className={className}>
      <div className="content">
        <h3>{"Support Hotlines"}</h3>
        <div className="actions">
          <div className="block">
            <div className="top">
              <h4>{"Call"}</h4>
              <p>{"MON TO SAT  9 AM - 6 PM IST"}</p>
            </div>
            <a href="tel:+917229970970" className="call">
              {"+917229970970"}
            </a>
          </div>
          <div className="block">
            <div className="top">
              <h4>{"WhatsApp"}</h4>
              <p>{"24 x 7 BOT SUPPORT"}</p>
            </div>
            <a
              href="https://wa.me/917229970970?text=Hello"
              target="_blank"
              className="whatsapp"
              rel="noreferrer nofollow"
            >
              {"+917229970970"}
            </a>
          </div>
        </div>
      </div>
    </section>
  );
};

export default HotLines;
