import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  "page-header": {
    backgroundColor: "transparent",
    margin: "0 auto",
    maxWidth: "90%",
    paddingTop: "56px",

    "& .content": {
      margin: "40px auto",
      maxWidth: "700px",

      "& .big-heading": {
        color: "#000000",
        fontSize: "36px",
        fontWeight: 900,
        lineHeight: "40px",
        marginBottom: "16px",
        textAlign: "center",
      },

      "& p": {
        color: "#24272C",
        fontSize: "16px",
        fontWeight: 500,
        lineHeight: "24px",
        textAlign: "center",
      },
    },
  },

  "new-customer": {
    backgroundColor: "transparent",
    margin: "0 auto 75px",
    maxWidth: "90%",

    "& .content": {
      margin: "0 auto",

      "& .block": {
        alignItems: "center",
        display: "flex",
        flexDirection: "column",
        justifyContent: "space-between",

        "& .col-left": {
          flexGrow: 1,

          "& img": {
            maxWidth: "320px",
            width: "100%",
          },
        },

        "& .col-right": {
          flexShrink: 1,

          "& .info": {
            maxWidth: "500px",
            textAlign: "center",

            "& h2": {
              color: "#24272C",
              fontSize: "36px",
              fontWeight: 900,
              lineHeight: "40px",
              marginBottom: "16px",
            },

            "& p": {
              color: "#24272C",
              fontSize: "16px",
              fontWeight: 500,
              lineHeight: "20px",
              marginBottom: "20px",
            },

            "& .actions": {
              alignItems: "center",
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",

              "& button": {
                alignItems: "center",
                backgroundColor: "#12B0EE",
                borderRadius: "8px",
                boxShadow: "none",
                color: "#ffffff",
                cursor: "pointer",
                display: "flex",
                justifyContent: "center",
                height: "48px",
                margin: 0,
                padding: 0,
                fontSize: "14px",
                fontWeight: 900,
                lineHeight: "14px",
                textTransform: "capitalize",
                width: "250px",

                "&.schedule": {
                  backgroundColor: "#ffffff",
                  color: "#12B0EE",
                  border: "2px solid #12B0EE",
                  borderRadius: "8px",
                  marginBottom: "16px",
                  transition: "all 0.2s ease",

                  "&:hover": {
                    backgroundColor: "#12B0EE",
                    color: "#ffffff",
                  },
                },
              },
            },
          },
        },
      },
    },
  },

  "old-customer": {
    backgroundColor: "transparent",
    margin: "0 auto 75px",
    maxWidth: "90%",

    "& .content": {
      "& .entry": {
        margin: "0 auto 32px",
        textAlign: "center",

        "& h2": {
          color: "#24272C",
          fontSize: "36px",
          fontWeight: 900,
          lineHeight: "40px",
          marginBottom: "16px",
        },

        "& p": {
          color: "#24272C",
          fontSize: "16px",
          fontWeight: 500,
          lineHeight: "24px",
          marginBottom: "16px",
        },
      },

      "& .info-blocks": {
        alignItems: "center",
        display: "flex",
        flexDirection: "column",
        justifyContent: "space-between",
        margin: "0 auto",
        textAlign: "center",

        "& .block": {
          display: "flex",
          flexDirection: "column",
          justifyContent: "space-between",
          maxWidth: "220px",
          margin: "0 auto 60px",

          "& .info": {
            color: "#24272C",

            "& .title": {
              fontSize: "30px",
              fontWeight: 900,
              lineHeight: "36px",
              marginBottom: "16px",
            },

            "& .desc": {
              fontSize: "16px",
              fontWeight: 500,
              lineHeight: "20px",
              marginBottom: "20px",
            },

            "& .link": {
              color: "#12B0EE",
              fontSize: "16px",
              fontWeight: 500,
              lineHeight: "20px",
              textDecoration: "none",
            },
          },
        },
      },
    },
  },

  "hot-lines": {
    backgroundColor: "transparent",
    margin: "0 auto 75px",
    maxWidth: "90%",
    paddingTop: "35px",
    paddingBottom: "35px",

    "& .content": {
      "& h3": {
        color: "#24272C",
        fontSize: "36px",
        fontWeight: 900,
        lineHeight: "40px",
        marginBottom: "45px",
        textAlign: "center",
      },

      "& .actions": {
        alignItems: "center",
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        columnGap: "100px",
        rowGap: "40px",

        "& .block": {
          maxWidth: "290px",
          width: "100%",

          "& .top": {
            alignItems: "center",
            display: "flex",
            justifyContent: "space-between",

            "& h4": {
              color: "#24272C",
              fontSize: "28px",
              fontWeight: 700,
              lineHeight: "48px",
            },

            "& p": {
              color: "#24272C",
              fontSize: "16px",
              fontWeight: 500,
              lineHeight: "18px",
              opacity: 0.4,
            },
          },

          "& a": {
            alignItems: "center",
            borderRadius: "12px",
            color: "#ffffff",
            display: "flex",
            justifyContent: "center",
            fontSize: "24px",
            fontWeight: 900,
            lineHeight: "24px",
            height: "64px",
            maxWidth: "100%",
            position: "relative",
            textDecoration: "none",
            width: "340px",

            "&::before": {
              content: '""',
              backgroundRepeat: "no-repeat",
              backgroundSize: "100% auto",
              display: "block",
              marginRight: "16px",
            },

            "&.call": {
              backgroundColor: "#12B0EE",
              boxShadow: "0px 8px 8px rgba(18, 176, 238, 0.1)",

              "&::before": {
                backgroundImage:
                  'url("data:image/svg+xml,%0A%3Csvg%20width%3D%2227%22%20height%3D%2227%22%20viewBox%3D%220%200%2027%2027%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%3Cpath%20d%3D%22M26.1728%2020.184L22.7541%2017.4268C21.9695%2016.8078%2020.8486%2016.7515%2020.0639%2017.3705L17.1496%2019.5088C16.8134%2019.7901%2016.309%2019.7339%2015.9727%2019.3962L11.6012%2015.4573L7.6781%2011.0681C7.34183%2010.7305%207.34183%2010.2803%207.56601%209.8864L9.6957%206.9603C10.3122%206.1725%2010.2561%205.04707%209.63966%204.25928L6.89348%200.826728C6.05281%20-0.186155%204.53961%20-0.298698%203.58685%200.657914L0.672534%203.58402C0.224178%204.03419%200%204.65317%200%205.27216C0.280223%2011.0118%202.85827%2016.4701%206.6693%2020.2966C10.4803%2024.123%2015.9166%2026.7115%2021.6332%2026.9929C22.2497%2027.0491%2022.8662%2026.7678%2023.3145%2026.3176L26.2288%2023.3915C27.2937%2022.5474%2027.2376%2020.9718%2026.1728%2020.184Z%22%20fill%3D%22white%22%2F%3E%3C%2Fsvg%3E")',
                height: "27px",
                width: "27px",
              },
            },

            "&.whatsapp": {
              backgroundColor: "#47C055",
              boxShadow: "0px 8px 8px rgba(71, 192, 85, 0.1)",

              "&::before": {
                backgroundImage:
                  'url("data:image/svg+xml,%0A%3Csvg%20width%3D%2239%22%20height%3D%2239%22%20viewBox%3D%220%200%2039%2039%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%3Cpath%20d%3D%22M28.8578%2022.4101C28.7819%2022.3736%2025.9387%2020.9735%2025.4334%2020.7917C25.2272%2020.7176%2025.0062%2020.6453%2024.7712%2020.6453C24.3873%2020.6453%2024.0648%2020.8367%2023.8135%2021.2125C23.5295%2021.6346%2022.6698%2022.6397%2022.4041%2022.9399C22.3694%2022.9795%2022.322%2023.0269%2022.2937%2023.0269C22.2682%2023.0269%2021.8282%2022.8457%2021.695%2022.7878C18.645%2021.4629%2016.33%2018.277%2016.0125%2017.7397C15.9672%2017.6625%2015.9653%2017.6274%2015.9649%2017.6274C15.9761%2017.5865%2016.0786%2017.4837%2016.1316%2017.4306C16.2864%2017.2774%2016.4542%2017.0754%2016.6165%2016.8801C16.6934%2016.7875%2016.7704%2016.6948%2016.846%2016.6074C17.0815%2016.3334%2017.1863%2016.1207%2017.3079%2015.8743L17.3716%2015.7463C17.6684%2015.1566%2017.4149%2014.659%2017.333%2014.4983C17.2657%2014.3638%2016.0652%2011.4664%2015.9376%2011.162C15.6306%2010.4275%2015.2251%2010.0854%2014.6615%2010.0854C14.6092%2010.0854%2014.6615%2010.0854%2014.4421%2010.0947C14.1751%2010.106%2012.7208%2010.2974%2012.0778%2010.7027C11.396%2011.1326%2010.2424%2012.5029%2010.2424%2014.9128C10.2424%2017.0818%2011.6188%2019.1297%2012.2098%2019.9085C12.2245%2019.9281%2012.2514%2019.968%2012.2906%2020.0253C14.5537%2023.3304%2017.375%2025.7798%2020.235%2026.9223C22.9885%2028.0222%2024.2923%2028.1493%2025.0336%2028.1493C25.0337%2028.1493%2025.0337%2028.1493%2025.0337%2028.1493C25.3452%2028.1493%2025.5945%2028.1249%2025.8145%2028.1032L25.954%2028.0899C26.9052%2028.0056%2028.9955%2026.9224%2029.471%2025.6011C29.8455%2024.5604%2029.9443%2023.4233%2029.6951%2023.0107C29.5244%2022.7301%2029.2302%2022.5889%2028.8578%2022.4101Z%22%20fill%3D%22white%22%2F%3E%3Cpath%20d%3D%22M19.8462%200C9.28375%200%200.690575%208.5286%200.690575%2019.0116C0.690575%2022.4022%201.59796%2025.7211%203.31687%2028.6259L0.0268148%2038.3309C-0.034471%2038.5119%200.0111135%2038.7119%200.144954%2038.8482C0.241568%2038.9468%200.372624%2039%200.506465%2039C0.557747%2039%200.609409%2038.9923%200.659806%2038.9762L10.7796%2035.7605C13.5488%2037.2401%2016.6787%2038.0211%2019.8463%2038.0211C30.4077%2038.0212%2039%2029.4935%2039%2019.0116C39%208.5286%2030.4077%200%2019.8462%200ZM19.8462%2034.0609C16.8656%2034.0609%2013.9787%2033.2003%2011.4971%2031.5719C11.4137%2031.5171%2011.3168%2031.489%2011.2193%2031.489C11.1678%2031.489%2011.1161%2031.4968%2011.0659%2031.5128L5.99648%2033.1242L7.63296%2028.2962C7.68589%2028.1399%207.65943%2027.9676%207.56192%2027.8344C5.6722%2025.2523%204.67326%2022.2015%204.67326%2019.0116C4.67326%2010.7123%2011.4798%203.96027%2019.8461%203.96027C28.2113%203.96027%2035.0171%2010.7123%2035.0171%2019.0116C35.0172%2027.3099%2028.2116%2034.0609%2019.8462%2034.0609Z%22%20fill%3D%22white%22%2F%3E%3C%2Fsvg%3E")',
                height: "39px",
                width: "39px",
              },
            },
          },
        },
      },
    },
  },

  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {
    "page-header, new-customer, old-customer, hot-lines": {
      maxWidth: "95%",
    },
  },
  [theme.breakpoints.up("md")]: {
    "page-header": {
      maxWidth: "1100px",
      paddingTop: "67px",
      width: "90%",

      "& .content": {
        margin: "60px auto",

        "& .big-heading": {
          fontSize: "58px",
          lineHeight: "64px",
        },
      },
    },

    "new-customer": {
      marginBottom: "55px",
      maxWidth: "1100px",
      width: "90%",

      "& .content": {
        backgroundColor: "#ffffff",
        border: "1px solid rgba(0, 0, 0, 0.1)",
        borderRadius: "12px",
        boxShadow: "0px 4px 40px 10px rgba(18, 176, 238, 0.1)",
        margin: "0 auto",
        maxWidth: "1072px",
        padding: "60px 75px 75px",

        "& .block": {
          flexDirection: "row",
          columnGap: "30px",

          "& .col-left": {
            marginRight: "30px",

            "& img": {
              width: "unset",
            },
          },

          "& .col-right": {
            "& .info": {
              "& h2": {
                fontSize: "42px",
                lineHeight: "48px",
                marginBottom: "26px",
              },

              "& .actions": {
                flexDirection: "row",

                "& button": {
                  width: "140px",

                  "&.schedule": {
                    marginBottom: 0,
                    marginRight: "40px",
                  },
                },
              },
            },
          },
        },
      },
    },

    "old-customer": {
      maxWidth: "1100px",
      width: "90%",

      "& .content": {
        "& .entry": {
          marginBottom: "75px",
          maxWidth: "515px",

          "& h2": {
            fontSize: "48px",
            lineHeight: "48px",
          },
        },

        "& .info-blocks": {
          flexDirection: "row",
          maxWidth: "960px",
          textAlign: "left",

          "& .block": {
            flexDirection: "row",
            maxWidth: "unset",
            marginBottom: 0,

            "&:not(:last-child)": {
              marginRight: "15px",
            },

            "& .icon": {
              marginRight: "30px",
            },

            "& .info": {
              color: "#24272C",

              "& .title": {
                marginBottom: "40px",
              },
            },
          },
        },
      },
    },

    "hot-lines": {
      maxWidth: "1100px",
      paddingTop: "75px",
      paddingBottom: "75px",
      width: "90%",

      "& .content": {
        "& h3": {
          fontSize: "48px",
          lineHeight: "30px",
          marginBottom: "50px",
        },

        "& .actions": {
          flexDirection: "row",

          "& .block": {
            maxWidth: "unset",
            width: "auto",

            "& a": {
              fontSize: "30px",
            },
          },
        },
      },
    },
  },
  [theme.breakpoints.up("lg")]: {
    "page-header, new-customer, old-customer, hot-lines": {
      maxWidth: "80%",
    },
  },
  [theme.breakpoints.up("xl")]: {
    "page-header, new-customer, old-customer, hot-lines": {
      maxWidth: "70%",
    },
  },
}));

export default useStyles;
