import React, { Fragment } from "react";
// react-helmet
import { Helmet } from "react-helmet";

// components
import Header from "../components/Header";
import Footer from "../components/Footer";
import PageHeader from "./PageHeader";
import NewCustomer from "./NewCustomer";
import ExistingCustomer from "./ExistingCustomer";
import HotLines from "./HotLines";

// styles
import "react-phone-input-2/lib/material.css";
import useStyles from "./styles/muiStyles";

const Contact = () => {
  const classes = useStyles();

  return (
    <Fragment>
      {/* Head */}
      <Helmet>
        <title>
          Contact us for for details and demo of WhatsApp Chatbot and support
          for exsisting account.
        </title>
        <link rel="canonical" href="https://easysocial.io/contact" />
        <meta
          name="keywords"
          content="WhatsApp Chatbot for Real Estate, WhatsApp Chatbot for Automobile Dealership, WhatsApp Chatbot for Banking, WhatsApp Chatbot for Insurance"
        />
        <meta
          name="description"
          content="We can create a customised whats app chat bot as per your requirement. Connect with us for a demo for your use case and industry."
        />
      </Helmet>
      {/* Main Header */}
      <Header />
      {/* Page Header */}
      <PageHeader className={classes["page-header"]} />
      {/* New Customer */}
      <NewCustomer className={classes["new-customer"]} />
      {/* Existing Customer */}
      <ExistingCustomer className={classes["old-customer"]} />
      {/* Hot Lines */}
      <HotLines className={classes["hot-lines"]} />
      {/* Footer */}
      <Footer />
    </Fragment>
  );
};

export default Contact;
