import React, { useState } from "react";
import { Button } from "@mui/material";

import PopUpCall from "../../components/PopUpCall";
import PopUpDemo from "../../components/PopUpDemo";

import NewCustomerImg from "../assets/new-customer.svg";

const PageHeader = ({ className }) => {
  const [callModal, setCallModal] = useState(false),
    [demoModal, setDemoModal] = useState(false),
    openDemoModal = () => setDemoModal(true),
    closeDemoModal = (event, reason) => {
      setDemoModal(false);
    },
    openCallModal = () => setCallModal(true),
    closeCallModal = (event, reason) => {
      setCallModal(false);
    };

  return (
    <section className={className}>
      <div className="content">
        <div className="block">
          <div className="col-left">
            <img src={NewCustomerImg} alt="New Customer" />
          </div>
          <div className="col-right">
            <div className="info">
              <h2>{"New to EasySocial?"}</h2>
              <p>
                {
                  "Want to explore how easysocial can help you scale customer support & automate digital marketing?"
                }
              </p>
              <p>
                {
                  "Talk to our Solutions advisor to suggest you the best solution that maximizes your business outcome with minimum expenditure."
                }
              </p>
              <div className="actions">
                <Button
                  variant="outlined"
                  className="schedule"
                  onClick={openCallModal}
                >
                  {"Schedule a Call"}
                </Button>
                <Button
                  variant="contained"
                  className="demo"
                  onClick={openDemoModal}
                >
                  {"Get a Demo"}
                </Button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <PopUpCall open={callModal} onClose={closeCallModal} />
      <PopUpDemo open={demoModal} onClose={closeDemoModal} />
    </section>
  );
};

export default PageHeader;
