import React from "react";

import EnquireImg from "../assets/enquire.svg";
import RenewImg from "../assets/renew.svg";
import CancelImg from "../assets/cancel.svg";

const ExistingCustomer = ({ className }) => {
  return (
    <section className={className}>
      <div className="content">
        <div className="entry">
          <h2>{"Existing Customer?"}</h2>
          <p>
            {
              "Do more with your EasySocial account, lets connect and understand how we can enable your business to grow faster."
            }
          </p>
        </div>
        <div className="info-blocks">
          {[
            {
              iconImg: EnquireImg,
              title: "Enquire",
              desc: "Get in touch with our support team for any general enquiry regarding product.",
              contactLink: "support@easysocial.io",
            },
            {
              iconImg: RenewImg,
              title: "Renew",
              desc: "Let our accounts team help you understand the most suited plan for your needs.",
              contactLink: "accounts@easysocial.io",
            },
            {
              iconImg: CancelImg,
              title: "Cancel",
              desc: "We stand by you even if wish to cancel your suscription at any point.",
              contactLink: "cancel@easysocial.io",
            },
          ].map(({ iconImg, title, desc, contactLink }, index) => (
            <div className="block" key={index}>
              {iconImg && (
                <div className="icon">
                  <img src={iconImg} alt="Icon" />
                </div>
              )}
              <div className="info">
                {title && <h4 className="title">{title}</h4>}
                {desc && <p className="desc">{desc}</p>}
                {contactLink && (
                  <a href={`mailto:${contactLink}`} className="link">
                    {contactLink}
                  </a>
                )}
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default ExistingCustomer;
